import React from 'react'
import {Link} from 'react-router-dom'
import {UserOutlined,GiftOutlined,ContainerOutlined,BankOutlined} from '@ant-design/icons'
import styles from './footer.module.css'
const navTab = [
  "/home",
  "/shop",
  "/detail",
  // "/cart",
  // "/order",
  "/user",
]
// 路由映射表
const navIcons = {
  "/shop": {
    title:'点餐',
    icon:<GiftOutlined />
  },
  "/home": {
    title:'首页',
    icon:<BankOutlined />
  },
  "/detail": {
    title:'订单',
    icon:<ContainerOutlined />
  },
  // "/cart": {
  //   title:'购物车',
  //   icon:<CarOutlined />
  // },
  "/user": {
    title:'我的',
    icon:<UserOutlined />
  },
  // "/order" : 123,
}
// const pathName = window.document.location.pathname;
export default function footer() {
  return (
    <ul className={styles['footer-nav']}>
      {
        navTab.map(item => {
          // const pathNames = pathName === item?'foorr':null;
          console.log(item);
          return(
          <li key={item} >
            <Link to={item} className={styles[`footer-nav-item`]}>
              <span className={styles['footer-nav-icon']}>
              {navIcons[item].icon}</span>{navIcons[item].title}
            </Link>
          </li>)
        })
      }
    </ul>
  )
}
