import React from 'react'

import styles from './userCard.module.css'
export default function userCard() {
  return (
    <div className={styles["user-card"]}>
      <div className={styles["user-left"]}>
        <div className={styles["user-icon"]}>
        123
        </div>
        <div className={styles["user-info"]}>
          <p className={styles["user-name"]}>俞</p>
          <div className={styles["user-experience"]}>
            <span className={styles["user-experience-rates"]}>
              <span className={styles["user-experience-rate"]}></span>
              {/* <span className={styles["user-experience-rate-green"]}></span> */}
            </span>
            <span className={styles["user-experience-number"]}>
              16/100
            </span>
          </div>
        </div>
      </div>
      <div className={styles["user-right"]}>
        <div className={styles["user-card-other"]}>
          <div className={styles["user-integral"]}>
            <p>1</p>
            <p>积分</p>
          </div>
          <div className={styles["user-integral"]}>
            <p>1</p>
            <p>奈雪券</p>
          </div>
          <div className={styles["user-integral"]}>
            <p>3</p>
            <p>会员码</p>
          </div>
        </div>
      </div>
    </div>
  )
}
