import React,{Component} from 'react';
import { Carousel } from 'antd';

import UserCard from '../../components/userCard/userCard'
import ShoppingType from '../../components/shoppingType/shoppingType'
import RecommendList from '../../components/recommendList/recommendList'
import styles from './home.module.css'

const recommendList = [
  { id: 1, title: '奈雪的茶商城', description:'好茶随行 美好常在', url:'www.baidu.com', image:''},
  { id: 2, title: '储值有礼', description:'储值100送50券包', url:'www.baidu.com', image:'' },
  { id: 3, title: '奈雪团单', description:'团购选优惠', url:'www.baidu.com', image:'' }
];

export default class Home extends Component {
  render() {
    return (
      <div className="home">
        <header>
          <Carousel autoplay>
            <img className={styles['home-carousel']} src='../../static/home-carousel-1.jpg' alt='' />
            <img className={styles['home-carousel']} src='../../static/home-carousel-2.jpg' alt='' />
          </Carousel>
        </header>
        <div className={styles["home-container"]}>
          <UserCard></UserCard>
          <ShoppingType></ShoppingType>
          <RecommendList list={recommendList} />
          <div className={styles['home-container-access']}>
            <h2>会员新鲜事</h2>
            <span className={styles['home-container-access-more']}>更多 {'>'} </span>
          </div>
          <div className={styles['home-container-access-card']} />
          <div className={styles['home-container-access-card']} />
        </div>
    </div>
    )
  }
}
